$mainColor: #470077;

.item {
  margin: 1rem 0;

  .image {
    width: 100%;
    height: 20rem;
    overflow: hidden;
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;

    img {
      width: 100%;
      object-fit: cover;
    }
  }

  .content {
    text-align: center;
    padding: 1rem;

    h3 {
      font-size: 1.25rem;
      color: #2c292b;
    }
  }

  .actions {
    padding: 1.5rem;
    text-align: center;

    button {
      font: inherit;
      cursor: pointer;
      color: $mainColor;
      border: 1px solid $mainColor;
      background-color: transparent;
      padding: 0.5rem 1.5rem;
      border-radius: 4px;
      transition: all 300ms;
    }

    button:hover, button:active{
        background-color: #efe2ff;
    }
  }
}
